import TagSexAge from '../common/tag-sex-age.vue'
export default {
	props: {
		item: Object,
		index: Number
	},
	components: {
		TagSexAge
	},
	data() {
		return {
			isguangzhu: this.item.isguangzhu
		}
	},
	methods: {
		guangzhu() {
			this.isguangzhu = true
			uni.showToast({
				title: '关注成功！'
			})
		},
		// 预览图片
		imgDetail(index) {
			uni.previewImage({
				current: index,
				urls: this.item.morepic
			})
		}
	}
}
